(function(){
    require('./arrayShuffle');
    let wordList = ['あ','い','う','え','お','か','き','く','け','こ','さ','し','す','せ','そ','た','ち','つ','て','と','な','に','ぬ','ね','の','は','ひ','ふ','へ','ほ','ま','み','む','め','も','や','ゆ','よ','ら','り','る','れ','ろ','わ','を','ん']
    let inOrderHira = new Vue({
        el: '#inOrderHira-page',
        data: {
            wordBackUp: Array.from(wordList),
            word: Array.from(wordList).shuffle(),
            fraction: 0
        },
        methods: {
            inOrder: function(e, event){
                if(e==this.wordBackUp[0]){
                    $('.card.mdui-color-red-a400').removeClass('mdui-color-red-a400 no-drop');
                    $(event.currentTarget).addClass('no-drop mdui-color-green');
                    $(event.currentTarget).removeClass('mdui-color-red-a400');
                    this.wordBackUp.shift();
                    this.fraction++;
                }else{
                    $(event.currentTarget).addClass('mdui-color-red-a400 no-drop')
                    $(event.currentTarget).removeClass('mdui-color-green')
                    this.fraction--;
                }
            },
            reset: function(){
                $('.card').removeClass('mdui-color-green mdui-color-red-a400 no-drop');
                this.word = Array.from(wordList).shuffle();
                this.wordBackUp = Array.from(wordList)
                this.fraction = 0;
            }
        }
    })
}())